@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Noto+Sans&family=Poppins&family=Roboto:wght@400;500;700&family=Source+Sans+Pro&display=swap);




.fs-imageStoryForm-root {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 5rem;
  gap: 5rem;
}

.fs-imageStoryForm-main {
  --size: 500px;
  width: var(--size);
  height: var(--size);
  border: 1px solid;
  border-radius: 1rem;
  position: relative;
}

.fs-fileUploader-root {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.fs-fileUploader-label svg {
  --size: 50px;
  width: var(--size);
  height: var(--size);
}

.fs-imageStoryForm-canvas {
  width: 100%;
  height: 100%;
}

.imageForm {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.fs-imageStoryForm-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 5rem;
  gap: 5rem;
}

.fs-dropdown-wrapper {
  position: absolute;
  right: 0;
  width: 100%;
  border: 1px solid #e0e0e0;
  box-sizing: border-box;
  border-radius: 12px;
  padding: 15px;
  background: #fff;
  min-width: 200px;
  z-index: 2;
}

.fs-dropdown-list {
  padding: 0;
}

.fs-dropdown-listItem {
  padding: 10px 15px;
  transition: all 0.5s;
  border-radius: 5px;
}

.fs-dropdown-listItem:hover {
  cursor: pointer;
  background: var(--light-1);
}

.fs-dropdown-listItem a {
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  color: var(--gray-2);
}

.fs-dropdown-listItem a svg {
  --size: 16px;
  width: var(--size);
  height: var(--size);
}

.fs-dropdown-listItem a p {
  font-size: 12px;
  font-weight: 500;
}

.fs-selector-wrapper {
  position: relative;
  z-index: 2;
  border: 1px solid;
  border-radius: 5px;
}

.fs-selector-selectedItem {
  font-size: 14px;
  font-weight: 500;
  display: flex;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  align-items: center;
  cursor: pointer;
  padding: 1rem;
}

.fs-selector-selectionItem {
  font-size: 14px;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
}

.fs-selector-selectedItem svg,
.fs-selector-selectionItem svg {
  --size: 16px;
  width: var(--size);
  height: var(--size);
}

.fs-textStoryForm-root {
  max-width: 50%;
  margin: 0 auto;
}

.fs-textStoryForm-form {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 50px;
  gap: 50px;
}

.fs-textStoryForm-textConfig {
  padding: 20px;
  box-shadow: 0px 0px 5px 0px rgb(0 0 0 / 20%);
  border-radius: 10px;
  background: #fff;
}

.fs-textStoryForm-textConfig__label {
  font-size: 12px;
  margin: 10px 0;
  font-weight: 500;
  border-top: 1px solid var(--gray-4);
  padding-top: 10px;
  padding: 0;
}

.fs-textStoryForm-textConfig__textArea {
  padding: 10px;
  border-radius: 8px;
  resize: none;
}

.fs-textStoryForm-textConfig__fontPicker {
  margin-bottom: 20px;
}

.fs-textStoryForm-textConfig__fontPickerValue {
  font-size: 12px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  width: 100%;
}

.fs-textStoryForm-textConfig__backgroundList {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(40px, 1fr));
  grid-gap: 0.5px;
  grid-gap: 10px;
  grid-gap: 10px;
  gap: 10px;
  margin: 0;
  padding: 0;
  margin-bottom: 10px;
  border-bottom: 1px solid var(--gray-4);
  padding-bottom: 20px;
}

.fs-textStoryForm-textConfig__backgroundList li {
  list-style: none;
  --size: 30px;
  width: var(--size);
  height: var(--size);
  border-radius: 50%;
}

.fs-textStoryForm-preview {
  background: #fff;
  border-radius: 10px;
  padding: 10px 30px 30px 30px;
  box-shadow: 0px 0px 5px 0px rgb(0 0 0 / 20%);
}

.fs-textStoryForm-preview__wrapper {
  --size: 400px;
  width: var(--size);
  height: var(--size);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}

.fs-textStoryForm-preview__text {
  font-size: 20px;
  color: #fff;
  word-break: break-all;
  padding: 15px;
}

.fs-textStoryForm-actionBtnGroup {
  display: flex;
  justify-content: flex-end;
  grid-gap: 20px;
  gap: 20px;
}

.fs-textStoryForm-actionBtnGroup__saveBtn,
.fs-textStoryForm-actionBtnGroup__cancelBtn {
  padding: 8px 24px;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 20px;
}

.fs-textStoryForm-actionBtnGroup__cancelBt {
  background: var(--red);
}

.fs-imageStoryViewer-canvas {
    width: 100%;
    height: 100%;
    border: 1px solid;
}

.fs-textStoryViewer-root {
  width: 100%;
  height: 100%;
  border: 1px solid;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
}

.fs-textStoryViewer-text {
  color: #fff;
  font-size: 22px;
}

